'use client';
import { useEffect, useState } from 'react';

const API_ORDERS_URL = 'http://oms-backend.test/api/orders';

export default function OrderSummary({ refreshTrigger }) {
  const [orders, setOrders] = useState([]);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState(null);

  const fetchOrders = async () => {
    try {
      setLoading(true);
      const res = await fetch(API_ORDERS_URL);
      if (!res.ok) throw new Error('Failed to fetch orders');
      const data = await res.json();
      setOrders(data);
    } catch (err) {
      console.error('Error fetching orders:', err);
      setError(err.message);
    } finally {
      setLoading(false);
    }
  };

  // ✅ Run fetch on mount and whenever refreshTrigger changes
  useEffect(() => {
    fetchOrders();
  }, [refreshTrigger]);

  if (loading) return <div>Loading orders...</div>;
  if (error) return <div className="text-red-500">Error: {error}</div>;

  return (
    <div className="mt-8">
      <h2 className="text-2xl font-semibold mb-4">Order Summary</h2>
      {orders.length === 0 ? (
        <p>No orders found.</p>
      ) : (
        <div className="space-y-4">
          {orders.map((order) => (
            <div key={order.id} className="border rounded-lg p-4 shadow-sm bg-white">
              <div className="flex justify-between mb-2">
                <div>
                  <p className="font-semibold">Order #: {order.order_number}</p>
                  <p className="text-sm text-gray-600">Status: {order.status}</p>
                  <p className="text-sm text-gray-600">
                    Total: ₱{parseFloat(order.total_amount).toLocaleString()}
                  </p>
                  <p className="text-xs text-gray-500">
                    {new Date(order.created_at).toLocaleString()}
                  </p>
                </div>
              </div>

              {order.items && order.items.length > 0 && (
                <div className="mt-3 border-t pt-2">
                  <h4 className="font-medium mb-2">Items:</h4>
                  <ul className="space-y-1 text-sm">
                    {order.items.map((item, i) => (
                      <li key={i} className="flex justify-between">
                        <span>
                          {item.product_name} × {item.quantity}
                        </span>
                        <span>
                          ₱{(item.unit_price * item.quantity).toLocaleString()}
                        </span>
                      </li>
                    ))}
                  </ul>
                </div>
              )}
            </div>
          ))}
        </div>
      )}
    </div>
  );
}
